html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.5;
}

p {
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.resaltar-texto {
  margin-top: 0;
  font-size: 4rem;
  font-weight: 700;
  color: 900;
}

.texto-gris {
  color: #626262;
}

.texto-chico {
  font-size: 1.5rem;
  text-align: start;
  color: #333333;
  font-weight: 900;
  margin-bottom: 1rem;
}

.texto-negro {
  font-weight: 700;
  color: #000000;
}

.btn-transparente {
  background-color: transparent;
  color: #FFFFFF;
  text-decoration: none;
  font-weight: 700;
  padding: 1rem 3rem;
  text-align: center;
  display: inline-block;
  border: none;
  border: 2px solid #FFFFFF;
}
.btn-transparente:hover {
  background-color: #FFFFFF;
  color: #0c62fb;
  cursor: pointer;
}

.btn-azul {
  background-color: #FFFFFF;
  color: #0c62fb;
  text-decoration: none;
  font-weight: 700;
  padding: 1rem 3rem;
  text-align: center;
  display: inline-block;
  border: none;
  border: 2px solid #0c62fb;
}
.btn-azul:hover {
  background-color: #0c62fb;
  color: #FFFFFF;
  cursor: pointer;
}

.header__contenido {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 40rem;
}
.header__contenido h1 {
  text-align: start;
  width: 60rem;
  color: #FFFFFF;
  font-size: 6rem;
  margin: 0;
  font-weight: 400;
}

.header__barra {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 3rem 0 0 0;
}
.header__barra .header__navegacion {
  display: flex;
  justify-content: space-between;
}

.header__logo {
  font-size: 3.7rem;
  color: #FFFFFF;
  font-weight: 900;
}
.header__logo:hover {
  opacity: 0.5;
}

.header__navegacion .enlace {
  margin-right: 2rem;
  font-size: 1.8rem;
  color: #FFFFFF;
  font-weight: 700;
}
.header__navegacion .enlace:hover {
  cursor: pointer;
  text-decoration: underline;
}

.header__buscador {
  display: flex;
  justify-content: flex-end;
}
.header__buscador p {
  display: inline;
}

.header {
  position: relative;
  min-height: 50rem;
  max-height: 70rem;
  overflow: hidden;
}
.header .overlay {
  position: absolute;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.2);
  z-index: 1;
}
.header .header__video {
  width: 100%;
  display: block;
}

.que-hacemos {
  width: min(90rem, 90%);
  margin: 0 auto;
}
.que-hacemos a {
  color: #0c62fb;
}
.que-hacemos a:hover {
  text-decoration: underline;
}
.que-hacemos a h2 {
  text-align: start;
  font-size: 2.2rem;
  font-weight: 900;
}
.que-hacemos .resaltar-texto {
  margin-top: 0;
  font-size: 4rem;
  font-weight: 700;
  color: 900;
}

.proyectos {
  margin-bottom: 4rem;
}
@media (min-width: 768px) {
  .proyectos {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}
.proyectos .proyecto-tarjeta {
  position: relative;
  margin-top: 2rem;
}
@media (min-width: 768px) {
  .proyectos .proyecto-tarjeta {
    margin-top: 0;
  }
}
.proyectos .proyecto-tarjeta:hover {
  cursor: pointer;
}
.proyectos .proyecto-tarjeta:hover .proyecto-tarjeta__contenido {
  background-color: #0c62fb;
}
.proyectos .proyecto-tarjeta:hover .proyecto-tarjeta__contenido p,
.proyectos .proyecto-tarjeta:hover .proyecto-tarjeta__contenido span {
  color: #FFFFFF;
}
.proyectos .proyecto-tarjeta:hover .proyecto-tarjeta__contenido span {
  font-weight: 900;
}
.proyectos .proyecto-tarjeta__contenido {
  position: absolute;
  z-index: 1;
  margin-top: -5rem;
  right: 0px;
  background-color: #f2f2f2;
  width: 85%;
}

.footer {
  margin-top: 2rem;
  background-color: #000000;
  padding: 2rem;
}

.seccion {
  margin-top: 5rem;
  margin-bottom: 5rem;
}
.seccion a {
  color: #0c62fb;
}
.seccion a:hover {
  text-decoration: underline;
}
.seccion a h2 {
  text-align: start;
  font-size: 2.2rem;
  font-weight: 900;
}

.plataformas__contenido {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.vision__contenedor {
  display: grid;
  grid-template-columns: 1fr 2fr;
  column-gap: 5rem;
}

.cuadro {
  display: flex;
  justify-content: end;
}
.cuadro .cuadro--azul {
  padding: 2rem 7rem;
  width: 70rem;
  background-color: #0c62fb;
  color: #FFFFFF;
  display: flex;
  justify-content: space-between;
  align-items: center;
}